import React, { useState } from 'react';

import styled from 'styled-components';
import { Layout as pLayout, Button, Card as pCard, Switch, Modal } from 'antd';

import ContentWithParallax from './Components/ContentWithParallax';
import { useDeviceOrientation } from './Hooks/UseDevcieOrientation';

const pHeader = pLayout.Header;
const pFooter = pLayout.Footer;
const pContent = pLayout.Content;

const boxShadow = '7px 7px 0px 0px rgba(0, 0, 0, .25)';

const AppContainer = styled.div`
  height: 100%;
  display: block;
  text-align: center;
`;

const Header = styled(pHeader)`
  background: #7dbcea;
  box-shadow: ${boxShadow};
`;

const Layout = styled(pLayout)`
  height: 100%;
`;

const Footer = styled(pFooter)`
  background: #7dbcea;
`;

const Content = styled(pContent)`
  flex: 1;
`;

const CardWrapper = styled.div`
  position: relative;
  width: 100%;
  height: 100%;
`;

const Card = styled(pCard)`
  display: inline-block;
  width: 100%;
  max-width: 300px;
  margin: 0 15px;
  box-shadow: ${boxShadow};
  top: 50%;
  transform: translateY(-50%);
`;

function App() {
  const [gyroEnabled, setGyroEnabled] = useState(false);
  const [parallaxValue, setParallaxValue] = useState(0);
  const [modalOpened, setModalOpened] = useState(false);
  const { orientation, requestAccess, revokeAccess } = useDeviceOrientation();

  const onToggle = (toggleState: boolean): void => {
    setGyroEnabled(toggleState);
    if (toggleState) {
      requestAccess();
    }
    else {
      revokeAccess();
    }
  };

  if (gyroEnabled && !modalOpened && orientation !== null && orientation.gamma !== null && orientation.alpha !== null) {
    let newValue = orientation.alpha * 50;
    const mql = window.matchMedia("(orientation: portrait)");

    if (mql.matches && orientation.beta) {
      newValue = orientation.beta * 50;
    }

    if (parallaxValue !== newValue) {
      setParallaxValue(newValue);
    }
  }

  return (
    <AppContainer onMouseMove={({ clientX: x, clientY: y }) => { if (!gyroEnabled && !modalOpened) { setParallaxValue(x); } }}>
      <Layout>
        <Header>
          <h1>
            Payks VR Demos: Overview
          </h1>
        </Header>

        <Content>
          <ContentWithParallax parallaxValue={parallaxValue}>
            <CardWrapper>
              <Card>
                <h3>VR/360 demos designed for Oculus Quest 2</h3>
                <p>These demos are build using Babylonjs and Typescript</p>
                <Button type="primary" onClick={() => setModalOpened(true)}>Start demo overview</Button>
              </Card>
            </CardWrapper>
          </ContentWithParallax>
        </Content>

        <Footer>
          Use Gyroscope <Switch onChange={onToggle} />
        </Footer>
      </Layout>

      <Modal
        visible={modalOpened}
        title="Overview"
        footer={null}
        onCancel={() => setModalOpened(false)}
      >
        <p>Some contents...</p>
        <p>Some contents...</p>
        <p>Some contents...</p>
        <p>Some contents...</p>
        <p>Some contents...</p>
      </Modal>
    </AppContainer>
  );
}

export default App;
